<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                平台活动
            </p>
        </section>
        <section class="middle">
            <div class="figure">
                <img :src="detail.picUrl" alt="">
            </div>
            <p class="article-title" v-html="detail.title"></p>
            <p class="collect">
                <span v-if="detail.isCollection" @click="recollect"><img width="15px" src="../../../assets/technology/icon_Collected@2x.png"><span style="font-size: 14px">已收藏</span></span>
                <span v-else @click="collect"><img width="15px" src="../../../assets/technology/icon_Uncollected@2x.png"><span style="font-size: 14px">收藏</span></span>
            </p>
            <div class="cover">
                <ul>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <span>活动类型:{{ detail.typeName }}</span>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_red@2x.png" alt="">
                        <span>活动主办方:{{ detail.sponsor }}</span>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <span>活动地点:{{ detail.site }}</span>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_red@2x.png" alt="">
                        <span>活动费用:{{ detail.charge }}</span>
                    </li>
                    <li>
                        <img src="../../../assets/technology/item_blue@2x.png" alt="">
                        <span>活动报名时间:{{ detail.startTime.substr(0, 10) }} - {{ detail.endTime.substr(0, 10) }}</span>
                    </li>
                </ul>
            </div>
        </section>
        <hr>
        <section class="bottom">
            <div class="title red-line">
                活动详情
            </div>
            <div class="article" v-html="detail.detail">
            </div>
        </section>
        <div class="button" @click="docking">
            <img src="@/assets/activity/icon_signup@2x.png" alt="">
            我要报名
        </div>
        <md-dialog
                :closable="true"
                v-model="iconDialog.open"
                :btns="iconDialog.btns"
        >
            <img src="../../../assets/activity/img@2x.png" alt="">
            <p>报名成功</p>
        </md-dialog>
    </main>
</template>
<script>
import { getDetail, join, collect, recollect } from '@/apiUrl/activity'
import { Icon, Toast, ActivityIndicator, Landscape, TabBar, Swiper, SwiperItem, Dialog } from 'mand-mobile'
export default {
  name: 'news_detail',
  components: {
    [Icon.name]: Icon,
    [ActivityIndicator.name]: ActivityIndicator,
    [Landscape.name]: Landscape,
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [Dialog.name]: Dialog
  },
  data () {
    return {
      collected: false,
      current: 1,
      items: [{ name: 1, label: '成果简介' }, { name: 2, label: '应用说明' }, { name: 3, label: '效益分析' }],
      detail: {},
      iconDialog: {
        open: false
      }
    }
  },
  mounted () {
    this.currentId = this.$route.query.id
    this.getDetail()
  },
  methods: {
    getDetail () {
      getDetail({ id: this.currentId }).then(res => {
        this.detail = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    docking () {
        this.$router.push({ path: '/submission/activity', query: { id: this.currentId }})
      // join({ id: this.currentId }).then(res => {
      //   this.iconDialog.open = true
      // }).catch(err => {
      //   if (!err instanceof Error) {
      //     Toast.failed(err)
      //   }
      // })
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    // 取消收藏
    recollect () {
      const args = {
        collectionId: this.currentId,
        type: 1
      }
      recollect(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 收藏
    collect () {
      const args = {
        id: this.currentId,
        type: 1
      }
      collect(args).then(res => {
        Toast.succeed('收藏成功')
        this.getDetail()
        this.collected = true
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.$refs.swiper.goto(index)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-bottom 0!important
        padding-top 88px
        .middle
            width 690px
            margin 40px auto
            .figure
                margin 30px auto 0
                img
                    width 690px
                    border-radius 8px
            .article-title
                font-size 32px
                color #333333
                text-align left
                line-height 48px
                margin-bottom 34px
            .time
                width 690px
                margin 0 auto
                display flex
                justify-content left
                font-size 24px
                span
                    display flex
                    align-items center
                    color #333333
                    img
                        margin-right 10px
                .line
                    margin 0 20px
                    color #999999
                    border-left 1px solid #999999
            .collect
                width 690px
                margin 36px auto
                display flex
                justify-content left
                font-size 28px
                span
                    display flex
                    align-items center
                    color #333333
                    img
                        margin-right 10px
            .cover
                background rgba(255, 255, 255, 1)
                border-radius 6px
                margin-bottom 40px
                padding-top 20px
                border-top 1px dashed rgba(153,153,153,0.3)
                ul
                    display flex
                    flex-wrap wrap
                    justify-content space-between
                    li
                        font-size 28px
                        color #666666
                        flex 0 0 100%
                        text-align left
                        margin 10px auto
                        display flex
                        img
                            width 26px
                            height 26px
                            margin-right 11px
        hr
            border none
            border-bottom 24px solid #EEEEEE
            margin 30px auto
        .bottom
            margin 0 auto
            padding-bottom 150px
            width 690px
            .cover
                background rgba(255, 255, 255, 1)
                box-shadow: 1px 1px 8px 0px rgba(2, 25, 85, 0.2)
                border-radius 6px
                padding 20px 30px
                margin-bottom 40px
                ul
                    display flex
                    flex-wrap wrap
                    justify-content space-between
                    li
                        font-size 28px
                        color #666666
                        flex 0 0 45%
                        text-align left
                        margin 20px auto
                        img
                            width 26px
                            height 26px
                            margin-right 11px
            .title
                width 690px
                height 66px
                line-height 66px
                color #333333
                font-size 28px
                text-indent 30px
                text-align left
                margin-bottom 30px
                background rgba(153, 153, 153, 0.1)
                .md-tab-bar
                    width 80%
                /deep/.md-tab-bar-item
                    height 66px!important
                    min-height 66px!important
            .red-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_red@2x.png") left center no-repeat
                background-size 13px 66px
            .article
                width 690px
                padding 28px 32px
                font-size 24px
                color #666666
                border 1px solid rgba(153, 153, 153, 0.3)
                border-radius 8px
                line-height 36px
                text-align left
                margin-bottom 30px
            .blue-line
                background rgba(153, 153, 153, 0.1) url("../../../assets/technology/line_blue@2x.png") left center no-repeat
                background-size 13px 66px
            .link
                font-size 24px
                color #021955
                padding-left 18px
                text-align left
                margin-bottom 52px
        .button
            position fixed
            bottom 0
            display: flex
            align-items center
            justify-content center
            width 100%
            height 98px
            background rgba(229,232,238,1);
            font-size 32px
            box-shadow 0px 2px 12px 0px rgba(2,25,85,0.36);
            img
                width 36px
                height 42px
                margin-right 19px
    .md-landscape-content
        padding 30px
        font-size 30px
        line-height 1.5
        color #666
        p
            margin-bottom 30px
 /deep/.md-dialog-content
            width 480px
 /deep/.md-dialog-body
            display flex
            flex-direction column
            align-items center
            justify-content center
            img
                width 240px
            p
                margin-top 30px
</style>
